/* MEDIA QUERIES
-------------------------------------*/

@media (min-width: 769px) {
    #overlay {
      background:rgba(0,0,0,0.7);
      width:100%;
      height:120%;
      position:fixed;
      top:0;
      left:0;
      display:none;
      text-align:center;
		img {
     		margin-top: 80px; 
    	}
    	h3 {
     		color:white; 
    	}
	}
	p {
		text-align: justify;	
	}
	#pic-caption {
		font-size: 1em;	
	}	
	/* GITHUB AD STYLES
	----------------------*/	
  #ad {
  	display: block;
    @include gradient(grey, purple);
  	color: white;
  	height: 120px;
  	width: 100%;
  	position:relative;
	h1 {
  		float: left;
  		margin-left: 50px;
  		padding: 10px;
  		text-shadow: 3px 3px 10px black;
	}
	h4 {
  		margin-left: 100px;
  		float: left;
  		clear: both;
	}

	a {
  		color: white;
	}
	a:hover {
  		color: gray;
	}
  }
  
  /*linkedin ad styles
  ---------------------------*/
  #linkedinad {
	background-image:url('../img/linkedin.png');
 	display: block;
 	position: relative;
 	padding: 20px;
 	margin-bottom: 50px;
 	margin-left: 10%;
 	margin-right: 10%;
 	font-family: $fjalla;
 	max-height: 110px;
 	h2 {
 		color: white;
		display: inline-block;
		font-weight: bold;
		&:hover {
			color: lightgray;	
		} 
	 }
 	img {
 		margin-left: 20px;
		margin-top: -10px;
 	}	
}
	.box {
		border-top: 1px solid white;
	}
	#meowButton {
		margin-top: 100px;	
	}
	
	#topbar {
		#navbar {
			float: none;
		}	
		ul {
			text-align: left;
			margin-top: 0;		
		}
		li {
			font-size: 1.3em;
			margin-left: 10px;
			margin-right: 10px;		
		}	
	}
}